$clock-diameter: 500px;
$clock-minute-height: 225px;
$clock-hour-height: 100px;
$clock-hand-width: 10px;

$position-top-minute: $clock-diameter / 2 - $clock-minute-height;
$position-top-hour: $clock-diameter / 2 - $clock-hour-height;
$position-left-clock: $clock-diameter / 2 - $clock-hand-width / 2;
